.benefits {
	padding: 25px 0 25px;
	background-color: #d8e7f9;
	&__item {
		& + & {
			margin-top: 25px; }
		&:nth-child(2) {
			padding-left: 20px;
			@include sm {
				padding-left: 15px; } }
		&-pic {
			display: inline-block;
			vertical-align: middle;
			&::after {
				position: relative;
				display: block;
				transform: scale(1.15);
				content: ''; }

			&_pic-1::after {
				@include sr('benefit_1'); }
			&_pic-2::after {
				@include sr('benefit_2'); }
			&_pic-3::after {
				@include sr('benefit_3'); } }
		&-text {
			display: inline-block;
			vertical-align: middle;
			width: calc(100% - 65px);
			max-width: 276px;
			line-height: (26/20);
			padding-left: 10px;
			color: #595858;
			font-size: 16px;
			@include sm {
				max-width: 260px;
				font-size: 15px; } } } }
