.contacts {
	&__info {
		padding: 10px 0 5px;
		& + & {
			margin-top: 20px; } }
	&__info-data {
		padding-bottom: 10px; }
	&__info-title {
		padding: 10px 0 25px;
		font-size: 21px;
		text-align: center; }
	&__info-mail {
		margin-bottom: 5px; }
	&__info-mail a {
		color: #008CFF;
		text-decoration: underline;
		&:hover {
			text-decoration: none; } }
	&__info-map iframe {
		width: 100%;
		height: 360px; }
	&__info-item {
		font-size: 16px; }
	&__info-descr_no-wrap {
		white-space: nowrap; }
	&__info-text + &__info-text {
		margin-top: 20px; }

	&__socials {
		padding: 25px 0;
		border: 8px solid #ebecec;
		border-top: 0;
		border-bottom: 0; }
	&__socials-links {
		@include f('psb', 17);
		text-align: center;
		a {
			color: #274F7F;
			text-decoration: underline;
			&:hover {
				text-decoration: none; } } }
	&__descr {
		padding: 25px 0 40px;
		font-size: 18px;
		&-title {
			margin-bottom: 20px;
			@include f('oss');
			line-height: 1.4;
			text-align: center; }
		&-text {
			font-size: 17px;
			text-indent: 35px; } }

	&__wait-text {
		padding-bottom: 30px;
		text-transform: uppercase;
		text-align: center;
		font-size: 18px; } }


/*  payment
	----------------------------------- */
.payment {
	position: relative;
	&__in {
		background: #FAFEFF;
		padding: 15px 10px 5px; }
	&__title {
		margin-bottom: 10px;
		color: #000;
		font-size: 21px;
		text-align: center; }
	&__descr {
		font-size: 15px;
		color: #1A2F4A; }
	&__list {
		max-width: 160px;
		margin: 30px auto 0;
		@include f('oss', 16);
		text-align: center;
		li {
			position: relative;
			padding-left: 35px;
			text-align: left;
			&:not(:last-child) {
				margin-bottom: 8px; } }
		&-icon {
			position: absolute;
			top: -7px;
			left: 0;
			z-index: 1;
			fill: #AEC94D;
			font-size: 28px; } }

	&__bg-wrap {
		overflow-x: scroll; }
	&__bg {
		margin: 10px auto 0;
		position: relative;
		width: 700px;
		min-height: 370px;
		background: #FAFEFF;
		&-img {
			@include coverdiv;
			@include cover; } }

	&__wait-text {
		position: absolute;
		bottom: 20px;
		left: 0;
		right: 0;
		padding: 0 10px;
		text-align: center;
		font-size: 18px; } }

/* delivery
	----------------------------------- */
.delivery {
	border-bottom: 5px solid #ebecec;
	&__title {
		position: relative;
		text-align: center;
		z-index: 2;
		padding-top: 5px; }
	&__country {
		padding: 15px 0 20px;
		&-in {
			position: relative;
			margin-top: 20px;
			padding-top: 90px; }
		&-text {
			position: absolute;
			top: 0;
			left: 30px;
			z-index: 1;
			@include f('asd', 22);
			color: #1C487A;
			line-height: 1; }
		&-pic {
			overflow-x: scroll;
			margin: 0 -10px;
			padding-bottom: 20px;
			img {
				width: 750px; } } }
	&__city-wrap {
		padding: 5px 0;
		overflow-x: scroll; }
	&__city {
		position: relative;
		padding-top: 10px;
		width: 625px;
		min-height: 475px;
		background: #def2fd;
		&-bg {
			@include coverdiv;
			@include cover; }
		&-text {
			position: relative;
			max-width: 350px;
			padding-left: 10px;
			text-align: left;
			z-index: 5; }
		&-descr {
			padding: 15px 0 0;
			font-size: 16px;
			p + p {
				margin-top: 15px; } } } }

/* news
	----------------------------------- */
.news {
	&__post {
		padding: 8px 0;
		& + & {
			margin-top: 20px; }
		&-img-wrap {
			img {
				width: 100%;
				height: 100%; } }
		&-date {
			display: flex;
			justify-content: space-between;
			padding: 10px;
			@include f('oss', 14);
			color: #BCBFC2; }
		&-title {
			margin-bottom: 15px;
			@include f('oss', 20);
			background: #F4F8FD;
			padding: 10px;
			font-size: 16px;
			text-align: center; }
		&-descr {
			position: relative;
			padding: 0 8px 40px;
			min-height: 245px;
			p {
				text-indent: 20px; }
			&_important {
				font-weight: bold; } }
		&-descr.is-open.news__post-descr_important {
			border-color: transparent; }
		&-more-btn {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			bottom: 10px;
			z-index: 1;
			color: #004D87;
			font-size: 16px;
			span {
				display: inline-block;
				vertical-align: middle; }
			&.is-active .news__post-more-icon {
				@include rotate(180); } }
		&-more-icon {
			display: inline-block;
			vertical-align: middle;
			fill: #999999;
			font-size: 13px; } } }
