.rating__input ~ .rating__icon .rating__star { fill: #DBDBDB; stroke: #DBDBDB; }

.rating__input:checked ~ .rating-icon .rating__star, .rating__input:checked .rating-icon .rating__star { fill: #FFED00; stroke: #FFED00; }

.rating__icon:hover .rating__star, .rating__icon:hover ~ .rating__icon .rating__star { fill: #C7CBCF; stroke: #DBDBDB; }

.rating__input:checked .rating__icon:hover .rating__star, .rating__input:checked ~ .rating__icon .rating__star { fill: #FFED00; stroke: #FFED00; }

.rating__icon { display: inline-block; cursor: pointer; -webkit-tap-highlight-color: transparent; }

.rating__icon:not(:last-child) { margin-left: 2px; }

.rating__star { font-size: 22px; }

/* pagination ----------------------------------- */
.pagination { font-size: 0; }

.pagination__list { font-size: 0; text-align: center; }

.pagination__list-item { display: inline-block; vertical-align: middle; margin: 0 2px; }

.pagination__list-item_prev { margin-right: 10px; text-align: center; }

.pagination__list-item_prev .pagination__list-link { padding: 0; line-height: 39px; }

@media only screen and (max-width: 1599px) { .pagination__list-item_prev .pagination__list-link { line-height: 34px; } }

@media only screen and (max-width: 424px) { .pagination__list-item_prev .pagination__list-link { line-height: 29px; } }

.pagination__list-item_next .pagination__icon-arr { top: 5px; left: 10px; transform: rotate(180deg); }

.pagination__list-item_dots { margin: 0 2px; }

.pagination__list-item_next .pagination__list-link { color: #768EA7; }

.pagination__list-item.is-active .pagination__list-link { background: #FFEBAB; }

.pagination__list-link { position: relative; display: inline-block; font-family: Arial, sans-serif; color: #125082; border-radius: 5px; padding: 4px; height: 30px; line-height: 24px; min-width: 31px; font-size: 18px; }

.pagination__list-link:hover { background: #EDEDED; }

.pagination__list-link:active { background: #C8D6E6; }

.pagination__list-link:hover .pagination__icon-arr { fill: #a6b5cc; }

.pagination__icon-arr { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); fill: #EDEDED; font-size: 20px; }

.pagination_product { padding: 30px 0; background: #fff; border: 5px solid #ebecec; border-top: 0; }

.pagination_watched { padding: 20px 0 25px; background: #ebf3fc; }

.pagination_watched .pagination__icon-arr { fill: #B8C6DB; }

.pagination_news { border-top: 4px solid #ebecec; padding: 30px 0; margin: 0 -10px; }

/* amount ----------------------------------- */
.amount { display: flex; justify-content: space-between; align-items: center; padding: 0 0 0 10px; background: #f4f8fd; }

.amount__text { font-size: 19px; color: #00306B; }

.amount__count { padding: 8px 10px 8px 25px; background-color: #eef5fc; border-radius: 30px 0 0 30px; }

.amount__input { margin-right: 15px; padding-right: 5px; font-size: 21px; color: #000; font-family: "Open Sans", Arial, sans-serif; font-weight: 600; width: 52px; height: 29px; line-height: 29px; text-align: right; }

/* summary ----------------------------------- */
.summ { padding: 20px 5px; margin-top: 10px; text-align: center; font-size: 17px; border-top: 5px solid #ebecec; }

.summ__text { color: #00306B; text-transform: uppercase; font-size: 18px; }

.summ__text-price, .summ__text-words, .summ__text-delivery { display: inline-block; }

.summ__text-price { margin: 0 9px; font-family: "PT Sans", Arial, sans-serif; font-weight: 700; }

.summ__text-price .icon-ruble { fill: #00306B; font-size: 13px; }

.summ__text-words, .summ__text-delivery { text-transform: none; }

.summ__text-delivery { color: #808080; }

.summ__promo { margin: 20px 0; }

.summ__promo-text { color: #B3B3B3; }

.summ__promo-input-wrap { max-width: 200px; margin: 3px auto 0; border: 1px solid #99AFCF; }

.summ__promo-input-wrap.is-focus { outline: 1px solid #99afcf; }

.summ__promo-input-wrap input { width: 100%; padding: 0 10px; font-family: "Verdana", Arial, sans-serif; font-size: 22px; height: 34px; line-height: 34px; font-size: 16px; }

/* entry pages ----------------------------------- */
.basket { padding-bottom: 5px; border: 5px solid #ebecec; }

.order { background: #ebecec; padding: 10px 10px 35px; }

.order__row { margin: 15px 0 0; text-align: center; }

.order__form-group-label { color: #999; font-family: "Verdana", Arial, sans-serif; font-size: 17px; }

.order__form-group-label + .order__form-group-label { margin-top: 10px; }

.order__form-group + .order__form-group { margin-top: 25px; }

.order__form-field { position: relative; padding-left: 48px; border: 1px solid #99AFCF; overflow: hidden; }

.order__form-field.is-focus { outline: 1px solid #99AFCF; }

.order__form-field::before { pointer-events: none; position: absolute; top: -3px; left: 0; width: 48px; height: 39px; content: ''; }

.order__form-field_name::before { background-image: url("../img/color-icons/user-log.svg"); background-repeat: no-repeat; background-size: contain; background-position: center center; }

.order__form-field_email::before { background-image: url("../img/color-icons/email-log.svg"); background-repeat: no-repeat; background-size: contain; background-position: center center; }

.order__form-field_pass::before { background-image: url("../img/color-icons/dots-log.svg"); background-repeat: no-repeat; background-size: contain; background-position: center center; }

.order__form-field_tel::before { background-image: url("../img/color-icons/phone-log.svg"); background-repeat: no-repeat; background-size: contain; background-position: center center; }

.order__form-field_text { margin-top: 20px !important; padding: 0 15px; text-align: center; border: none; }

.order__form-field_required { position: relative; }

.order__form-field_required::after { position: absolute; content: '*'; color: #E63833; font-family: "Verdana", Arial, sans-serif; font-size: 22px; font-weight: 700; top: 2px; right: 4px; font-size: 19px; }

.order__form-field_address { padding: 0; margin-top: 5px; }

.order__form-field_address textarea { display: block; width: 100%; min-height: 100px; padding: 5px 20px 10px 10px; resize: none; border: 0; font-family: "Verdana", Arial, sans-serif; font-size: 16px; }

.order__form-field + .order__form-field { margin-top: 15px; }

.order__form-ask { margin: 15px 0; text-align: center; font-size: 17px; }

.order__form-ask-in { margin-top: 15px; font-size: 0; }

.order__form-ask-in .order__check { margin: 0 15px; }

.order__input { width: 100%; font-family: "Verdana", Arial, sans-serif; font-size: 16px; color: #000; height: 34px; padding: 0 20px 0 10px; line-height: 34px; }

.order__input::placeholder { color: #999999; }

.order__check { display: inline-block; }

.order__comment { padding: 20px 0 0; }

.order__comment-in { padding: 10px 0; }

.order__comment-title { margin-bottom: 10px; font-family: "Verdana", Arial, sans-serif; font-size: 18px; color: #000; text-align: center; }

.order__comment-descr { color: #4D4D4D; max-width: 232px; margin: 0 auto 10px; text-align: center; font-size: 15px; }

.order__comment-message-wrap { position: relative; margin-bottom: 20px; padding-left: 48px; padding-bottom: 36px; border: 1px solid #99AFCF; background: #fff; overflow: hidden; }

.order__comment-message-wrap.is-focus { outline: 1px solid #99AFCF; }

.order__comment-message-wrap::before { content: ''; position: absolute; top: -1px; left: 0; z-index: 1; width: 48px; height: 38px; background-image: url("../img/color-icons/dots-log.svg"); background-repeat: no-repeat; background-size: contain; background-position: center center; }

.order__comment-message-wrap::after { content: ''; position: absolute; top: 0; left: 0; width: 48px; height: 100%; background: #f7f7f7; z-index: 0; }

.order__comment-message { display: block; width: 100%; padding: 6px 10px 0; resize: none; min-height: 120px; border: none; font-family: "PT Sans", Arial, sans-serif; font-size: 17px; font-weight: 400; transition: min-height 0.3s; }

.order__comment-message.extended { min-height: 190px; }

.order__comment-extend { position: absolute; right: 5px; bottom: 3px; width: 66px; height: 27px; padding-top: 5px; background: #F7F7F7; }

.order__comment-extend:hover { background: #E0E0E0; }

.order__comment-extend:hover .order__comment-extend-icon { fill: #56708A; }

.order__comment-extend:active .order__comment-extend-icon { fill: #103559; }

.order__comment-extend-icon { fill: #C4C4C4; font-size: 17px; }

.order__comment-extend.is-active .order__comment-extend-icon { transform: scaleY(-1); }

/* check ----------------------------------- */
.check__circle { display: inline-block; margin-left: 4px; border: 2px solid #99AFCF; border-radius: 50%; text-align: center; width: 20px; height: 20px; line-height: 14px; background: #fff; }

.check__circle::after { display: inline-block; border-radius: 50%; background: #768FAE; content: ''; transform: scale(0.4); opacity: 0; transition: .15s; width: 10px; height: 10px; }

.check label { color: #000; cursor: pointer; font-size: 17px; }

.check input { display: none; }

.check input:checked + label .check__circle::after { transform: scale(1); opacity: 1; }

.contacts__info { padding: 10px 0 5px; }

.contacts__info + .contacts__info { margin-top: 20px; }

.contacts__info-data { padding-bottom: 10px; }

.contacts__info-title { padding: 10px 0 25px; font-size: 21px; text-align: center; }

.contacts__info-mail { margin-bottom: 5px; }

.contacts__info-mail a { color: #008CFF; text-decoration: underline; }

.contacts__info-mail a:hover { text-decoration: none; }

.contacts__info-map iframe { width: 100%; height: 360px; }

.contacts__info-item { font-size: 16px; }

.contacts__info-descr_no-wrap { white-space: nowrap; }

.contacts__info-text + .contacts__info-text { margin-top: 20px; }

.contacts__socials { padding: 25px 0; border: 8px solid #ebecec; border-top: 0; border-bottom: 0; }

.contacts__socials-links { font-family: "PT Sans", Arial, sans-serif; font-size: 17px; font-weight: 700; text-align: center; }

.contacts__socials-links a { color: #274F7F; text-decoration: underline; }

.contacts__socials-links a:hover { text-decoration: none; }

.contacts__descr { padding: 25px 0 40px; font-size: 18px; }

.contacts__descr-title { margin-bottom: 20px; font-family: "Open Sans", Arial, sans-serif; font-weight: 600; line-height: 1.4; text-align: center; }

.contacts__descr-text { font-size: 17px; text-indent: 35px; }

.contacts__wait-text { padding-bottom: 30px; text-transform: uppercase; text-align: center; font-size: 18px; }

/*  payment ----------------------------------- */
.payment { position: relative; }

.payment__in { background: #FAFEFF; padding: 15px 10px 5px; }

.payment__title { margin-bottom: 10px; color: #000; font-size: 21px; text-align: center; }

.payment__descr { font-size: 15px; color: #1A2F4A; }

.payment__list { max-width: 160px; margin: 30px auto 0; font-family: "Open Sans", Arial, sans-serif; font-size: 16px; font-weight: 600; text-align: center; }

.payment__list li { position: relative; padding-left: 35px; text-align: left; }

.payment__list li:not(:last-child) { margin-bottom: 8px; }

.payment__list-icon { position: absolute; top: -7px; left: 0; z-index: 1; fill: #AEC94D; font-size: 28px; }

.payment__bg-wrap { overflow-x: scroll; }

.payment__bg { margin: 10px auto 0; position: relative; width: 700px; min-height: 370px; background: #FAFEFF; }

.payment__bg-img { position: absolute; top: 0; left: 0; width: 100%; height: 100%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.payment__wait-text { position: absolute; bottom: 20px; left: 0; right: 0; padding: 0 10px; text-align: center; font-size: 18px; }

/* delivery ----------------------------------- */
.delivery { border-bottom: 5px solid #ebecec; }

.delivery__title { position: relative; text-align: center; z-index: 2; padding-top: 5px; }

.delivery__country { padding: 15px 0 20px; }

.delivery__country-in { position: relative; margin-top: 20px; padding-top: 90px; }

.delivery__country-text { position: absolute; top: 0; left: 30px; z-index: 1; font-family: "ASimplerDnm", Arial, sans-serif; font-size: 22px; color: #1C487A; line-height: 1; }

.delivery__country-pic { overflow-x: scroll; margin: 0 -10px; padding-bottom: 20px; }

.delivery__country-pic img { width: 750px; }

.delivery__city-wrap { padding: 5px 0; overflow-x: scroll; }

.delivery__city { position: relative; padding-top: 10px; width: 625px; min-height: 475px; background: #def2fd; }

.delivery__city-bg { position: absolute; top: 0; left: 0; width: 100%; height: 100%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.delivery__city-text { position: relative; max-width: 350px; padding-left: 10px; text-align: left; z-index: 5; }

.delivery__city-descr { padding: 15px 0 0; font-size: 16px; }

.delivery__city-descr p + p { margin-top: 15px; }

/* news ----------------------------------- */
.news__post { padding: 8px 0; }

.news__post + .news__post { margin-top: 20px; }

.news__post-img-wrap img { width: 100%; height: 100%; }

.news__post-date { display: flex; justify-content: space-between; padding: 10px; font-family: "Open Sans", Arial, sans-serif; font-size: 14px; font-weight: 600; color: #BCBFC2; }

.news__post-title { margin-bottom: 15px; font-family: "Open Sans", Arial, sans-serif; font-size: 20px; font-weight: 600; background: #F4F8FD; padding: 10px; font-size: 16px; text-align: center; }

.news__post-descr { position: relative; padding: 0 8px 40px; min-height: 245px; }

.news__post-descr p { text-indent: 20px; }

.news__post-descr_important { font-weight: bold; }

.news__post-descr.is-open.news__post-descr_important { border-color: transparent; }

.news__post-more-btn { position: absolute; left: 50%; transform: translateX(-50%); bottom: 10px; z-index: 1; color: #004D87; font-size: 16px; }

.news__post-more-btn span { display: inline-block; vertical-align: middle; }

.news__post-more-btn.is-active .news__post-more-icon { transform: rotate(180deg); }

.news__post-more-icon { display: inline-block; vertical-align: middle; fill: #999999; font-size: 13px; }
