// header
.header {
	&__container {
		position: relative;
		padding: 20px 15px 10px;
		font-size: 0;
		@include sm {
			padding: 20px 10px 5px; }
		&::before {
			position: absolute;
			top: 0;
			bottom: 0;
			width: 100%;
			background-image: repeating-linear-gradient(to bottom, rgba(9, 161, 217, 0.2) 0%, rgba(72, 190, 233, 0) 54%, rgba(72, 190, 233, 0) 11%, rgba(72, 190, 233, 0) 77%, rgba(72, 190, 233, 0) 40%, #09a1d9 100%);
			z-index: 1;
			content: ''; }
		&::after {
			position: absolute;
			top: 0;
			left: -55%;
			right: -4%;
			bottom: 0;
			background-image: repeating-linear-gradient(90deg, #09A1D9 0%, #48bee9 54%, #48bee9 11%, #48bee9 90%, #48bee9 72%, #09A1D9 98%);
			z-index: 0;
			pointer-events: none;
			content: ''; } }
	&__bar-in {
		display: flex;
		background-color: #F8F8F8; }
	&__bar-btn {
		position: relative;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 25%;
		height: 65px;
		-webkit-tap-highlight-color: transparent;
		text-align: center;
		&:first-child, &:last-child {
			justify-content: flex-start; }
		&.is-active {
			background: #ebecec; }
		&.is-active::before {
			content: '';
			position: absolute;
			height: 2px;
			width: 100%;
			bottom: -2px;
			background: #ebecec; }
		&.is-active .hamburger__bar {
			background: #053b76;
			&::before, &::after {
				background: #053b76; } } }

	&__bar-btn.is-active &__bar-icon {
		fill: #194073; }
	&__bar-content {
		border-top: 1px solid #d9d9d9;
		> div {
			display: none; } }
	&__bar-icon {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		z-index: 1;
		fill: #999999;
		font-size: 38px;
		&.icon-user {
			font-size: 39px; } }
	&__hamburger {
		margin-left: 20px; }
	&__bottom {
		position: relative;
		background-color: #09A1D9;
		overflow: hidden;
		@include clr;
		&-col {
			display: inline-block;
			vertical-align: top;
			width: 50%; } }

	&__nav {
		position: relative;
		z-index: 10;
		text-align: right;
		.nav__list {
			position: relative;
			padding-right: 10px;
			height: 90px; } }
	&__top-links {
		position: relative;
		margin-top: 20px;
		z-index: 1; }

	&__search {
		padding: 15px 15px;
		background: #ebecec;
		@include r(374) {
			padding: 15px 4px; } }
	&__breadcrumbs {
		position: relative;
		padding: 5px 0;
		z-index: 1; }
	&__cart {
		padding: 15px 0;
		background: #ebecec;
		text-align: center;
		&-field {
			margin-bottom: 6px;
			& + & {
				margin-top: 3px;
				margin-bottom: 0; } }
		&-text {
			display: inline-block;
			padding: 2px 0 3px;
			border-bottom: 1px solid #CED2D6;
			color: #999999;
			font-size: 18px;
			&_title {
				color: #194073;
				border-bottom: none; }
			&_border {
				border-top: 1px solid #CED2D6; }
			span {
				@include f('oss'); } } }
	&__cart-btn {
		position: relative;
		width: 45px;
		margin-left: 15px;
		@include r(374) {
			margin-left: 4px; } }
	&__cart-btn-total {
		position: absolute;
		right: -21px;
		bottom: -4px;
		z-index: 1;
		min-width: 40px;
		padding: 2px 4px;
		border: 1px solid #999999;
		border-radius: 5px;
		@include f('oss',15,15);
		color: #4D4D4D;
		text-align-last: center;
		white-space: nowrap;
		background: #fff;
		&_quantity {
			display: none;
			bottom: 20px;
			right: -29px;
			&.has-items {
				display: block; } } }
	&__cart-btn-val {
		fill: currentColor;
		font-size: 11px; } }
// end header

// search
.search {
	&__field {
		position: relative;
		border: 1px solid transparent;
		&.is-focus {
			background-color: #5b98f5; } }
	&__input {
		padding: 0 45px 0 7px;
		height: 40px;
		line-height: 40px;
		width: 100%;
		@include f("psr", 17);
		background-color: #fff;
		border: 1px solid transparent;
		color: #000;
		@include trs;
		@include placeholder-color(#B3B3B3);
		@include r(374) {
			font-size: 16px; } }
	&__btn {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		width: 40px;
		height: 40px;
		background-color: #F5F5F5;
		border: 2px solid #fff;
		&::after {
			display: block;
			margin: 0 auto;
			content: '';
			width: 21px;
			height: 17px;
			transform: scale(1.2);
			@include st('search'); }
		&:active {
			-webkit-tap-highlight-color: transparent;
			background-color: #d6d6d6; } } }

// top links
.top-links {
	font-size: 0;
	text-align: center;
	&__link {
		display: inline-block;
		margin-bottom: 5px;
		width: calc(50% - 5px);
		padding: 0 5px;
		height: 38px;
		line-height: 33px;
		color: #fff;
		border: 2px solid rgba(255, 255, 255, 0.5);
		font-size: 18px;
		@include sm {
			font-size: 16px;
			width: calc(50% - 3px); }
		@include trs;
		&:nth-child(2n) {
			margin-left: 10px;
			@include sm {
				margin-left: 5px; } }
		&:hover {
			background-color: rgba(#385794, .3);
			border-color: #348EBF; } } }

// menu
.menu {
	&__hamburger {
		display: none;
		position: fixed;
		top: 30px;
		right: 20px;
		z-index: 101;
		opacity: .5;
		&:active {
			opacity: 1; }
		.hamburger__bar {
			background: #053b76;
			transition: background .3s;
			&::before, &::after {
				transition: background .3s; }
			&::before, &::after {
				background: #053b76; } }
		&.is-active {
			display: block; } }
	&__block {
		border-left: 10px solid #ebecec;
		border-right: 10px solid #ebecec;
		border-bottom: 25px solid #ebecec; }
	&__title {
		padding: 18px 0;
		text-align: center;
		background: #ebecec;
		&-text {
			display: inline-block;
			color: #194073;
			text-transform: uppercase;
			font-size: 20px;
			border-bottom: 2px solid #ced2d6; } }
	&__list {
		padding: 15px 0 20px;
		background: #fafafa;
		&:last-child, &:nth-child(4) {
			padding-bottom: 40px; } }
	&__item {
		padding: 0 30px;
		&:not(:last-child) {
			margin-bottom: 6px; } }
	&__link {
		display: block;
		padding: 2px 0;
		border-bottom: 1px solid #e6e6e6;
		color: #194073;
		font-size: 18px;
		&:hover {
			color: #000;
			border-bottom-color: #c1c1c1; } }
	&__rubric {
		&-open {
			position: relative;
			display: block;
			width: 100%;
			padding: 15px 35px;
			background: #fafafa;
			text-align: center;
			color: #194073;
			span {
				padding-bottom: 3px;
				border-bottom: 3px solid #ededed;
				font-size: 18px; }
			&.is-active {
				background: #d1d1d1;
				span {
					border-bottom-color: #b8b8b8;
					color: #fff; } } }
		&-arr {
			position: absolute;
			top: 15px;
			right: 15px;
			font-size: 10px;
			fill: #bbbcbd;
			&.is-active {
				background: #d1d1d1;
				color: #fff; } } }
	&__version-link {
		display: block;
		padding: 20px;
		background: #ebecec;
		text-align: center;
		font-size: 16px;
		color: #274F7F;
		span {
			border-bottom: 1px solid #274F7F; }
		&:hover span {
			border-bottom-color: transparent; } } }

.login {
	padding: 20px 10px 40px;
	background: #ebecec;
	&__in {
		max-width: 340px;
		margin: 0 auto; }
	&__text {
		margin-bottom: 16px;
		padding-left: 50px;
		font-size: 18px;
		color: #6A7B91;
		span {
			border-bottom: 2px solid #CED2D6; } }
	&__field {
		position: relative;
		padding: 0 0 0 46px;
		border: 1px solid #99AFCF;
		overflow: hidden;
		&::before {
			position: absolute;
			top: -1px;
			left: 0;
			width: 48px;
			height: 37px;
			content: ''; }
		&.is-focus {
			border-color: #5494F5; }
		& + & {
			margin-top: 15px; }
		& + &:nth-child(3) {
			margin-top: 30px; }
		&_name::before {
			@include ico('user-log'); }
		&_pass::before {
			@include ico('dots-log'); }
		&_email::before {
			@include ico('email-log'); } }
	&__input {
		width: 100%;
		height: 36px;
		padding: 0 10px;
		line-height: 36px;
		@include f('ver', 15);
		@include placeholder-color(#999999); }
	&__btn {
		display: block;
		margin: 18px auto 0;
		width: 100%;
		max-width: 230px;
		height: 44px;
		line-height: 42px;
		background: #b9c0c9;
		color: #fff;
		@include f('oss', 17);
		border: 1px solid transparent;
		border-bottom-color: #99afcf;
		text-transform: uppercase;
		-webkit-tap-highlight-color: transparent;
		&:active, &:hover {
			border-color: #000;
			background: #b9c0c9;
			color: #666666;
			span {
				border-bottom-color: #999999; } }
		span {
			border-bottom: 2px solid #a0aaba; } }

	&__registr {
		margin: 35px 0 10px; } }
